// .nk-sidebar-menu{height: 100%; overflow: auto;}
.nk-menu {
    .accordion-button {
        padding: 0;
        border-radius: 0px !important;
        background: transparent;
        box-shadow: none;
        &:after{
            background-image: none !important;
            font-weight: bold;
            width: auto;
            height: auto;
            position: absolute;
            font-family: $nk-dashlite-font;
            top: 50%;
            content: $menu-toggle-icon-ltr;
            right: 1.25rem;
            transform: translateY(-50%);
            font-size: 1rem;
            color: $base-500;
            transition: transform .3s, color .1s, opacity .3s ease .3s;
            @if($menu_dark_option==true or $menu_dark_theme_option==true) {
                 .is-dark &, .is-theme & {
                    color: rgba($white, 0.4);
                 }
            }
        }
        &:not(.collapsed){
            &:after {
                background-image: none !important;
                transform: translateY(-50%) rotate(90deg) !important;
            }
        }
    }
    &-link{
        width: 100%;
        &:after, &-normal:after{display: none;}
        &:hover, .active .accordion-button > &, .accordion-button:not(.collapsed) > &{
            color:$base-500 !important;
            background: $accent-color;
            .is-light &{
                background: $accent-color;
            }
            @if($menu_dark_option==true) {
                .is-dark & {
                    background: lighten($sidebar-bg-dark-color,8%);
                    color: $white;
                }
            }
            @if($menu_dark_theme_option==true) {
                .is-theme & {
                    background: lighten($sidebar-bg-theme-color, 6%);
                }
            }
            .count{
                color: $menu-link-color-hover;
            }
        }
    }
    &-icon{
        color: $base-500;
        .nk-menu-link:hover &, .nk-menu-item.active .accordion-button > .nk-menu-link & , .nk-menu-item.current-menu .accordion-button > .nk-menu-link &, .nk-menu-item .accordion-button:not(.collapsed) > .nk-menu-link &{
            color: $base-500;
            @if($menu_dark_theme_option==true) {
                .is-dark & {
                    color: $white;
                }
                .is-theme &{
                    color: $base-500;
                }
            }
        }
    }
    &-sub{
        .nk-menu-link {
            font-size: 15px;
            &:hover{
                color: $accent-color !important;
            }
            &:hover,.show .accordion-body > &{
                background-color: transparent;
            }
        }
        .active > .nk-menu-link {
            color: $accent-color;
            @if($menu_dark_theme_option==true) {
                .is-dark &{
                    color: $white;
                }
            }
        }
    }
}

