.choosePhoto{
    margin-inline-end: 0;
    img{ width: 100%; height: 180px;object-fit: cover;border-radius: 10px;}
    .ant-radio-inner{display: none;}
    span.ant-radio+*{padding:0;}
    &_txt{
        position: absolute;top: 25px;left: 15px;width: 117px;background-color: #fff;border-radius: 5px;
        padding:8px;text-align: right;font-size: 14px;color: #101010;
        &::before, &::after{
            content: '';
            position: absolute;
            top: 11px;
            left: 10px;
            width: 16px;
            height: 16px;
            text-align: center;
            background-color: transparent;
            border: 1px solid #101010;
            border-radius: 50%;
            @include transition(all 0.3s);
        }
    }
    .ant-radio-checked{
        & + {
            span{
                .choosePhoto_txt{
                    &::before{
                        border-color: $accent-color;
                    }
                    &:after{
                        width: 10px ;height: 10px ; background-color: $accent-color;border-color: $accent-color;
                        top: 14px;left: 13px;
                    }
                }
            }
        }
    }
    &_delete{
        width: 35px;height: 35px;background-color: #101010;border-radius: 50%;display: flex;align-items: center;justify-content: center;
        position: absolute;top: -10px;right: -3px;border: 3px solid #fff; transition: all 0.3s;
        em{color: #fff}
        &:hover{
            background-color: $accent-color;
            em{color: #fff}
        }
    }
    @include media-breakpoint-down(lg){
        img{ height: 130px; }
    }

    @include media-breakpoint-down(sm){
        width: 100%;
        .ant-radio+span{width: 100%;}
        
       
    }
}

