// datepicker 
.datePickerUser{
    .ant-picker {
        display: flex !important;
    
        &-dropdown {
            position: relative !important;
            top: 0 !important;
            left: 0 !important;
    
            .ant-picker-panel-container {
                box-shadow: none !important;
                margin-left: 0 !important;
            }
    
            .ant-picker-panel-layout,
            .ant-picker-panel-layout .ant-picker-panel,
            .ant-picker-date-panel,
            .ant-picker-header,
            .ant-picker-body,
            .ant-picker-content,
            .ant-picker-footer {
                width: 100% !important;
            }
    
            .ant-picker-cell {
    
                // display: flex!important;
                // align-items: center!important;
                // justify-content: center!important;
                &::before {
                    height: 35px !important;
                }
            }
    
            .ant-picker-cell-in-view {
                &.ant-picker-cell-today .ant-picker-cell-inner{
                    background-color: rgba(#000, 0.20)!important;
                    box-shadow: 0px 0px 0px 15px rgba(#000, 0.20);
                }
                .ant-picker-cell-inner {
                    width: 35px;
                    height: 35px;
                    margin: 0 auto;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    &:hover{
                        background-color: rgba(#000, 0.25);
                    }
                }
            }
    
            .ant-picker-cell-in-view.ant-picker-cell-selected .ant-picker-cell-inner,
            .ant-picker-cell-range-end .ant-picker-cell-inner  {
                color: #fff !important;
                background-color: #000 !important;
            }
    
            .ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-in-range::before,
            .ant-picker-cell-range-start::before,
            .ant-picker-cell-in-view.ant-picker-cell-in-range::before,
            .ant-picker-cell-in-view.ant-picker-cell-range-end:not(.ant-picker-cell-range-end-single)::before  {
                background-color: rgba(#000, 0.25) !important;
            }
        }
    
        .ant-picker-range-arrow,
        .ant-picker-active-bar {
            display: none !important;
        }
    }
}
//range slider
.ant-slider{
    .ant-slider-track{
       background-color: #3eae66;
    }
    .ant-slider-handle{
        width: 15px;
        &:after{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            box-shadow: 0px, 0px, 0px, 10px, rgba(#3eae66, 0.20);
        }
        &:after{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            box-shadow: 0px, 0px, 0px, 10px, rgba(#3eae66, 0.20);
        }
        &:hover, &:focus{
            &:after{
                width: 15px;height: 15px;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                box-shadow: 0px, 0px, 0px, 10px, rgba(#3eae66, 0.20);
            }
        }
    }
    .ant-slider-handle{
        &:after{
            width: 15px;
            height: 15px;
            background-color: #3eae66;
        }
    }
    &:hover{
        .ant-slider-track{
            background-color: #3eae66;
         }
        .ant-slider-handle{
            &:after{
                box-shadow: 0px, 0px, 0px, 10px, rgba(#3eae66, 0.20);
            }
        }
    }
}
.ant-slider-horizontal{ 
    .ant-slider-rail,.ant-slider-track, .ant-slider-step{
        height: 6px!important;
    }
}
.rangeSliderform-group{
    max-width: 700px;
    margin: 0 auto;
}
.rangeSliderList{
    display: flex;
    align-items: center;
    justify-content: center;
    &_list{
        padding: 0 20px;
        p{font-size: 18px;}
        .icon{
            color: #555555;
            margin: 0 auto;
            font-size: 24px;
        }
        &.active{
            .icon{
                color: #3eae66;

            }
        }
    }
}